import React from 'react';

const Start = ({handleStart}) => {
    return (
        <div className='card'>
            <div className="card-content">
                <h1 className='mb-4 title is-4'>    JavaScript  React!</h1>
                <h4 className='mb-2'>!</h4>
                <button className="button is-info" onClick={() => {handleStart()}}></button>
            </div>
        </div>
    );
};

export default Start;